import addMonths from 'date-fns/add_months'
import formatDate from 'date-fns/format'
import isAfter from 'date-fns/is_after'
import isBefore from 'date-fns/is_before'
import startOfMonth from 'date-fns/start_of_month'
import React, { Component } from 'react'

import HeaderButton from './header_button'

type Props = {
  activeMonth: any
  blockClassName?: string
  customRender?: (...args: any[]) => any
  headerNextArrow?: JSX.Element
  headerNextTitle?: string
  headerPrevArrow?: JSX.Element
  headerPrevTitle?: string
  maxDate?: any
  minDate?: any
  onMonthChange: (...args: any[]) => any
}

export default class MonthHeader extends Component<Props, {}> {
  _switchMonth(offset: -1 | 1) {
    const {onMonthChange, activeMonth} = this.props

    onMonthChange(addMonths(activeMonth, offset))
  }

  render() {
    const {
      activeMonth,
      minDate,
      maxDate,
      blockClassName,
      headerNextArrow,
      headerNextTitle,
      headerPrevArrow,
      headerPrevTitle,
      customRender,
    } = this.props

    const prevEnabled = minDate ? isBefore(startOfMonth(minDate), startOfMonth(activeMonth)) : true
    const nextEnabled = maxDate ? isAfter(startOfMonth(maxDate), startOfMonth(activeMonth)) : true

    if (customRender) {
      return customRender({
        ...this.props,
        prevEnabled,
        nextEnabled,
        switchMonth: this._switchMonth.bind(this),
        children: 'no content, please use activeMonth prop and custom buttons instead',
      })
    }

    return (
      <div className={`${blockClassName}-month_header`}>
        <HeaderButton
          type="prev"
          arrow={headerPrevArrow}
          title={headerPrevTitle}
          enabled={prevEnabled}
          onClick={this._switchMonth.bind(this, -1)}
          blockClassName={blockClassName}
        />
        <div className={`${blockClassName}-month_header_title`}>{formatDate(activeMonth, 'MMMM YYYY')}</div>
        <HeaderButton
          type="next"
          arrow={headerNextArrow}
          title={headerNextTitle}
          enabled={nextEnabled}
          onClick={this._switchMonth.bind(this, 1)}
          blockClassName={blockClassName}
        />
      </div>
    )
  }
}
