import classnames from 'classnames'
import React, { SFC } from 'react'

type Props = {
  arrow?: JSX.Element
  blockClassName?: string
  enabled?: boolean
  offset?: number
  onClick: (...args: any[]) => any
  title?: string
  type: 'prev' | 'next'
}

const HeaderButton: SFC<Props> = ({arrow, blockClassName, enabled, type, title, onClick}) => (
  <button
    className={classnames(`${blockClassName}-header_button`, `is-${type}`, {
      'is-disabled': !enabled,
    })}
    type="button"
    disabled={!enabled}
    title={title}
    onClick={onClick}
  >
    {arrow}
  </button>
)

export default HeaderButton
